@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
* {
  font-family: "Source Sans Pro", sans-serif;
}
.header {
  background-color: white;
  border-radius: 5px;
  opacity: 0.8;
  margin-bottom: 100px;
}

.card:hover {
  transform: scale(1.2);
}

.card {
  background-color: rgb(119, 140, 235);
  width: 200px;
  height: 100px;
  padding-top: 10px;
  margin-left: 20px;
  border-radius: 5%;
  color: white;
  align-content: center;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: transform 0.3s;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.card_inner2 {
  background-color: orange;
}

.card_inner3 {
  background-color: rgb(226, 3, 3);
}

.card_inner4 {
  background-color: rgb(6, 141, 35);
}

.card_main1,
.card_main2,
.card_main3,
.card_main4 {
  padding: 10px;
}

section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "poppins", sans-serif;
  min-height: 100vh;
  position: relative;
  background-image: url("https://c.tenor.com/sgjY8LfzJPsAAAAC/virus-blob.gif");
  background-repeat: no-repeat;
  background-size: 100%;
}

.live {
  text-align: center;
  animation: live 4s ease-out infinite normal;
  font-size: 2em;
  text-transform: uppercase;
}

@keyframes live {
  0% {
    transform: scale(0.9);
  }

  25% {
    transform: scale(1.2);
  }

  60% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(0.9);
  }
}

@media only screen and (max-width: 768px) {
  .card {
    width: 100%;
    margin-left: 0px;
    margin-bottom: 10px;
  }

  section {
    display: block;
    justify-content: stretch;
    /* border: 8px solid rgb(8, 90, 8); */
    width: 100%;
  }

  body {
    display: block;
    width: 100%;
  }

  .card:hover {
    transform: scale(1.1);
  }
}
